import { options } from './../constant/index';

const ValidationReducer = (state, action) => {
    switch (action.type) {
        case 'taiKhoan':
            return {
                ...state,
                values: {
                    ...state.values,
                    taiKhoan: action.payload,
                },
                errors: {
                    ...state.errors,
                    taiKhoan:
                        action.payload.length > 0
                            ? ''
                            : 'Vui lng nhp tn ti khon !',
                },
            };

        case 'hoTen':
            return {
                ...state,
                values: {
                    ...state.values,
                    hoTen: action.payload,
                },
                errors: {
                    ...state.errors,
                    hoTen:
                        action.payload.length > 0
                            ? ''
                            : 'Vui lng nhp h tn !',
                },
            };

        case 'email':
            let mess = '';
            const checkMail = action.payload.match(
                /^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i
            );

            if (action.payload.length === 0) {
                mess = 'Vui lng nhp email !';
            } else if (checkMail === null) {
                mess = 'Email khng hp l !';
            }
            return {
                ...state,
                values: {
                    ...state.values,
                    email: action.payload,
                },
                errors: {
                    ...state.errors,
                    email: mess,
                },
            };

        case 'soDt':
            let messPhone = '';
            const checkPhone = action.payload.match(
                /(84|0[3|5|7|8|9])+([0-9]{8})\b/g
            );

            if (action.payload.length === 0) {
                messPhone = 'Vui lng nhp s in thoi';
            } else if (checkPhone === null) {
                messPhone =
                    'S in thoi khng hp l. Vui lng nhp  10 s';
            }
            return {
                ...state,
                values: {
                    ...state.values,
                    soDt: action.payload,
                },
                errors: {
                    ...state.errors,
                    soDt: messPhone,
                },
            };

        case 'maNhom':
            return {
                ...state,
                values: {
                    ...state.values,
                    maNhom: action.payload,
                },
                errors: {
                    ...state.errors,
                    maNhom:
                        action.payload.length > 0
                            ? ''
                            : 'Vui lng nhp m nhm',
                },
            };

        case 'maLoaiNguoiDung':
            return {
                ...state,
                values: {
                    ...state.values,
                    maLoaiNguoiDung: action.payload,
                },
                errors: {
                    ...state.errors,
                    maLoaiNguoiDung:
                        action.payload.length > 0
                            ? ''
                            : 'vui lng nhp m ngi dng',
                },
            };

        case 'matKhau':
            let messPass = '';
            if (action.payload.length === 0) {
                messPass = 'Vui lng nhp mt khu';
            } else if (action.payload.length < 8) {
                messPass = 'Mt khu t nht 8 k t';
            }
            return {
                ...state,
                values: {
                    ...state.values,
                    matKhau: action.payload,
                },
                errors: {
                    ...state.errors,
                    matKhau: messPass,
                },
            };

        default:
            return {
                ...state,
                values: {
                    taiKhoan: '',
                    hoTen: '',
                    email: '',
                    soDt: '',
                    maNhom: '',
                    maLoaiNguoiDung: options[0].value,
                    matKhau: '',
                },
                errors: {
                    taiKhoan: '',
                    hoTen: '',
                    email: '',
                    soDt: '',
                    maNhom: '',
                    maLoaiNguoiDung: '',
                    matKhau: '',
                },
            };
    }
};

export default ValidationReducer;
